.work {
    padding: 2%;
}

.heading {
    margin: auto;
    background-color: var(--green);
    color: #000;
    padding: 1% 2% 1% 2%;
    width: 30%;
    text-align: center;
    border-radius: 10px;
}

.timeline {
    position: relative;
    width: 100%;
    max-width: 1140px;
    margin: 0 auto;
    padding: 15px 0;
}
  
.timeline::after {
    content: '';
    position: absolute;
    width: 2px;
    background: #006E51;
    top: 0;
    bottom: 0;
    left: 50%;
    margin-left: -1px;
}
  
.container {
    padding: 15px 30px;
    position: relative;
    background: inherit;
    width: 50%;
}
  
.container.left {
    left: -5%;
}
  
.container.right {
    left: 50%;
}
  
.container::after {
    content: '';
    position: absolute;
    width: 16px;
    height: 16px;
    top: calc(50% - 8px);
    right: -8px;
    background: #ffffff;
    border: 2px solid #006E51;
    border-radius: 16px;
    z-index: 1;
}
  
.container.right::after {
    left: -8px;
}
  
.container::before {
    content: '';
    position: absolute;
    width: 50px;
    height: 2px;
    top: calc(50% - 1px);
    right: 8px;
    background: #006E51;
    z-index: 1;
}
  
.container.right::before {
    left: 8px;
}
  
.container .date {
    position: absolute;
    display: inline-block;
    top: calc(50% - 8px);
    text-align: center;
    font-size: 14px;
    font-weight: bold;
    color: #006E51;
    text-transform: uppercase;
    letter-spacing: 1px;
    z-index: 1;
}
  
.container.left .date {
    right: -75px;
}
  
.container.right .date {
    left: -75px;
}
  
.container .icon {
    position: absolute;
    display: inline-block;
    width: 40px;
    height: 40px;
    padding: 1%;
    top: calc(50% - 20px);
    background: #F6D155;
    border: 2px solid #006E51;
    border-radius: 100%;
    text-align: center;
    font-size: 18px;
    color: #006E51;
    z-index: 1;
}
  
.container.left .icon {
    right: 56px;
}
  
.container.right .icon {
    left: 56px;
}
  
.container .content {
    padding: 30px 90px 30px 30px;
    background: #F6D155;
    position: relative;
    border-radius: 0 500px 500px 0;
}
  
.container.right .content {
    padding: 30px 30px 30px 90px;
    border-radius: 500px 0 0 500px;
}
  
.container .content h2 {
    margin: 0 0 10px 0;
    font-size: 18px;
    font-weight: normal;
    color: #006E51;
}
  
.container .content p {
    margin: 0;
    font-size: 16px;
    line-height: 22px;
    color: #000000;
}